import './Third.scss';

const SubTemplate = ({ subData }) => {
  return (
    <div>
      <ul className="sub_desc">
        {subData.map(it => {
          return (
            <li key={it.id}>
              <strong>{it.title}</strong>
              {it.linkFlag ?
                <ul className="link_list">
                  {it.contents.map((its, num) => {
                    return (
                      <li key={num}>
                        {num >= 1 ? <a href={its} target="_blank" rel="noopener noreferrer">{it.title.split(' / ')[num]}</a> : its}
                      </li>
                    )
                  })}
                </ul>
                :
                typeof it.contents === 'object' ?
                  <ul className="tech_list">
                    {it.contents.map((el, idx) => {
                      return (
                        <li key={idx}>
                          <figure className={`bg_set itm0${idx + 1}`}></figure>
                          <span>{el}</span>
                        </li>
                      )
                    })}
                  </ul>
                  :
                  <span>{it.contents}</span>
              }
            </li>
          )
        })}
      </ul>
    </div>
  )
}

const InfoTemplate = ({ data }) => {
  return (
    <div className="desc">
      <h5>{data.title}</h5>
      <div className="sub_info">
        <strong> </strong>
        <p>
          {data.intro}
        </p>
      </div>
      <SubTemplate subData={data.subData} />
    </div>
  )
}

