import './Second.scss';
import YouTube from 'react-youtube';

const YTP = ({ link }) => {
  const _onReady = (event) => {
    event.target.pauseVideo();
  }
  const opts = {
    height: '650rem',
    width: '100%',
    playerVars: {
      autoplay: 0,
      controls: 1,
      fs: 1,
      rel: 0,
      loop: 1,
      modestbranding: 1,
    }
  }
  return <YouTube videoId={link} opts={opts} onReady={_onReady} />
}

const PhaseTemplate = ({ data }) => {
  return (
    <div className="phase">
      <h5>
        {data.title}
      </h5>
      {data.icon ?
        <ul className="icon_list">
          {data.list.map((it, idx) => {
            return (
              <li key={idx}>
                <figure className={`bg_set itm0${idx + 1}`}></figure>
                <span>{it}</span>
              </li>
            )
          })}
        </ul>
        :
        <ul className="desc_list">
          {data.linkFlag ?
            data.list.map((it, idx) => {
              return (
                <li key={idx}>
                  {idx >= 1 ? <a href={it} target="_blank" rel="noopener noreferrer">{data.title.split(' / ')[idx]}</a> : it}
                </li>
              )
            })
            :
            data.list.map((it, idx) => {
              return (
                <li key={idx}>
                  {it}
                </li>
              )
            })}
        </ul>
      }
    </div>
  )
}

const Second = () => {
  const descList = [
    { id: 1, title: '  /  / README', icon: false, linkFlag: true, list: ['HEROKU', 'https://jungto-page.herokuapp.com/', 'https://skillful-mandevilla-651.notion.site/README-fee75e1f37a44267a4f44c192b775ce7'] },
    { id: 2, title: '', icon: false, linkFlag: false, list: ['CRUD ', '/ ', '  ', 'SPA '] },
    { id: 3, title: '', icon: true, linkFlag: false, list: ['scss', 'react', 'react-router-dom', 'ExpressJS', 'redux-toolkit', 'redux-persist', 'mongoDB'] },
  ];
  return (
    <>
      <div className="section bg_set second">
        <div className="container">
          <div className="left">
            <div className="title_box">
              <h4 className="div_title">MAIN PROJECT (REACT)</h4>
            </div>
            <div className="info_desc">
              <div className="main_info">
                <h5>  </h5>
                <strong> </strong>
                <p>
                     ,             . <br />,        .       MongoDB   . React ExpressJS, MongoDB   ,      .
                </p>
              </div>
              <div className="sub_info">
                {descList.map((it, idx) => {
                  return (
                    <PhaseTemplate key={idx} data={it} />
                  )
                })}
              </div>
            </div>
          </div>
          <div className="right">
            <h4 className="div_title">DEMONSTRATION</h4>
            <figure className="movie">
              <YTP link='UQSlJOFWOIE' />
            </figure>
          </div>
        </div>
      </div>
    </>
  )
}

export default Second;